<template>
  <div class="video-item">
    <router-link class="figure" :to="`/video/${video?.id}`">
      <img class="figure-pic lazyload-img" :src="video?.cover" />
      <div class="figure-caption">
        <span v-if="duration" class="video-duration">{{ duration }}</span>
      </div>
    </router-link>
    <div class="detail">
      <router-link class="detail-title webkit-text-ellipsis" :to="`/video/${video?.id}`">
        {{ video?.description || '' }}
      </router-link>
      <div class="detail-meta">
        <router-link class="author-info text-ellipsis" :to="`/user/${video?.user?.id}`">
          <img class="user-avatar" :src="video?.user?.avatar" />
          <span class="user-name">
            {{ video?.user?.name }}
          </span>
        </router-link>
        <span class="time-ago text-ellipsis">{{ video?.created_at }}</span>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { toRefs } from 'vue';
import moment from '@/common/moment';
import { PostItemInterface } from '@/graphql/schema';

export default {
  props: {
    video: Object as () => PostItemInterface
  },
  setup(props) {
    const { video } = toRefs(props);
    const duration = moment.format(video.value?.video?.duration)?.substr(-5);
    return { duration };
  }
};
