<template>
  <div id="video-center">
    <div class="fluid-content">
      <Skeleton v-if="loading" type="videos" />
      <div class="flow-items" v-infinite-scroll="loadMore" infinite-scroll-disabled="disabled">
        <div class="item-wrap" v-for="(item, index) in publicPosts" :key="index">
          <VideoItem :video="item" />
        </div>
      </div>
      <LoadingSpinner v-if="hasMorePages" />
    </div>
  </div>
</template>

<script lang="ts">
import { ref, watch } from 'vue';
import { usePublicPostsQuery } from '@/graphql';
import Skeleton from '@/components/Skeleton.vue';
import VideoItem from '@/components/VideoItem.vue';
import LoadingSpinner from '@/components/LoadingSpinner.vue';
import useItemsPerRow from '@/components/hooks/useItemsPerRow';

export default {
  components: {
    Skeleton,
    VideoItem,
    LoadingSpinner
  },
  setup() {
    const count = useItemsPerRow('--column-video-items-per-row');
    const variables = ref({
      first: count.value * 5
    });
    const { loading, disabled, loadMore, hasMorePages, publicPosts } = usePublicPostsQuery(variables);

    return {
      loading,
      disabled,
      loadMore,
      hasMorePages,
      publicPosts
    };
  }
};
</script>

<style lang="scss" scoped>
#video-center {
  position: relative;
  width: 100%;
  min-height: 100vh;
  margin-top: 30px;
}
</style>
