
import { toRefs } from 'vue';
import moment from '@/common/moment';
import { PostItemInterface } from '@/graphql/schema';

export default {
  props: {
    video: Object as () => PostItemInterface
  },
  setup(props) {
    const { video } = toRefs(props);
    const duration = moment.format(video.value?.video?.duration)?.substr(-5);
    return { duration };
  }
};
